import { mapState, mapMutations } from 'vuex';
export default {
	data() {
		return {
			show:false,
			mobile: '',
			isChecked: '1',
			btn: '获取验证码',
			time: 60,
			timer: null,
			canClick: true
		};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	computed: { ...mapState(['wxInfo']) },
	onUnload() {
		clearInterval(this.timer);
		this.timer = null;
	},
	methods: {
		...mapMutations(['setLogin']),
		onInput(e) {
			this.mobile = e.detail;
		},
		getCode() {
			if (!this.canClick) return false;
			this.canClick = false;
			console.log('获取验证码');
			if (this.time === 60) {
				this.$Fetch({
					url: 'login/wxCode',
					data: {
						mobile: this.mobile,
						open_id: this.wxInfo.openId,
						user_name: this.wxInfo.nickName,
						head_img: this.wxInfo.avatarUrl
					},
					success: res => {
						if(res==="请输入密码"){
							uni.showToast({
								title: '请填写密码！',
								icon: 'none'
							});
							this.show=true;
						}
						this.canClick = true;
						uni.showToast({
							title: '发送成功',
							icon: 'success'
						});
						this.timer = setInterval(() => {
							this.time--;
							this.btn = this.time + 's后重新获取';
							if (this.time < 0) {
								clearInterval(this.timer);
								this.timer = null;
								this.time = 60;
								this.btn = '获取验证码';
							}
						}, 1000);
					},
					complete: () => {
						this.canClick = true;
					}
				});
			}
		},
		onChange() {
			this.isChecked = this.isChecked == 1 ? '0' : '1';
		},
		onSubmit(e) {
			let that = this;
			console.log('提交数据：', e.detail.value);
			let data = e.detail.value;
			if (!data.server_people_id) {
				data.server_people_id = 0;
			}
			if (this.isChecked === '1') {
				this.$Fetch({
					url: 'login/wxBoundMobile',
					data: data,
					success: res => {
						uni.setStorage({
							key: 'TOKEN',
							data: res,
							success: function() {
								that.setLogin();
								uni.showToast({
									title: '绑定成功！',
									icon: 'success'
								});
								setTimeout(() => {
									uni.reLaunch({
										url: '/pages/index/index'
									});
								}, 1500);
							}
						});
					}
				});
			} else {
				uni.showToast({
					title: '未同意用于协议，不可注册！',
					icon: 'none'
				});
			}
		}
	}
};
